(function() {
  'use strict';

  angular
    .module('monitoring')
    .controller('PaasContainerController', PaasContainerController);

  /** @ngInject */
  function PaasContainerController($scope, $timeout, $sce, $location, $interval, $exceptionHandler, common, paasContainerService) {
    var vm = this;
    vm.scope = $scope;
    vm.Math = Math;
    vm.scope.loading = true;
    vm.common = common;

    // angular-gridster option
    vm.scope.cellOverviewOptions = {
      margins: [0, 15],
      columns: 5,
      rowHeight: 110,
      widget_base_dimensions: [140, 140],
      mobileModeEnabled: false,
      draggable: false,
      resizable: {enabled: false}
    };

    vm.scope.cellOverviewItems = [
      {id: 'running',  col: 0, row: 0, sizeY: 1, sizeX: 1, name: "Running",  count: 'cellRunning',  color: "#00aacc"},
      {id: 'failed',   col: 1, row: 0, sizeY: 1, sizeX: 1, name: "Failed",   count: 'cellFailed',   color: "#e66b6b"},
      {id: 'critical', col: 2, row: 0, sizeY: 1, sizeX: 1, name: "Critical", count: 'cellCritical', color: "#ad6de8"},
      {id: 'warning',  col: 3, row: 0, sizeY: 1, sizeX: 1, name: "Warning",  count: 'cellWarning',  color: "#f0a141"},
      {id: 'total',    col: 4, row: 0, sizeY: 1, sizeX: 1, name: "Total",    count: 'cellTotal',    color: "#909090"}
    ];

    vm.scope.containerOverviewOptions = {
      margins: [0, 15],
      columns: 4,
      rowHeight: 110,
      widget_base_dimensions: [140, 140],
      mobileModeEnabled: false,
      draggable: false,
      resizable: {enabled: false}
    };

    vm.scope.containerOverviewItems = [
      {id: 'running',  col: 0, row: 0, sizeY: 1, sizeX: 1, name: "Running",  count: 'containerRunning',  color: "#00aacc"},
      {id: 'critical', col: 1, row: 0, sizeY: 1, sizeX: 1, name: "Critical", count: 'containerCritical', color: "#ad6de8"},
      {id: 'warning',  col: 2, row: 0, sizeY: 1, sizeX: 1, name: "Warning",  count: 'containerWarning',  color: "#f0a141"},
      {id: 'total',    col: 3, row: 0, sizeY: 1, sizeX: 1, name: "Total",    count: 'containerTotal',    color: "#909090"}
    ];


    // Cell Overview
    (vm.getCellOverview = function() {
      paasContainerService.cellOverview().then(
        function(result) {
          vm.scope.cellRunning = result.data.running;
          vm.scope.cellFailed = result.data.failed;
          vm.scope.cellCritical = result.data.critical;
          vm.scope.cellWarning = result.data.warning;
          vm.scope.cellTotal = result.data.total;
          vm.scope.loadingCellOverview = false;
        },
        function(reason) {
          vm.scope.loadingCellOverview = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();


    // Container Overview
    (vm.getContainerOverview = function() {
      paasContainerService.containerOverview().then(
        function(result) {
          vm.scope.containerRunning = result.data.running;
          vm.scope.containerCritical = result.data.critical;
          vm.scope.containerWarning = result.data.warning;
          vm.scope.containerTotal = result.data.total;
          vm.scope.loadingContainerOverview = false;
        },
        function(reason) {
          vm.scope.loadingContainerOverview = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();


    // Container Summary
    vm.scope.currentPage = 1;             // 
    vm.scope.itemsPerPage = 10;           //   
    vm.scope.selSearchCondition = 'name';

    (vm.getContainerSummary = function(summaryObj, flag) {
      if(summaryObj == undefined || summaryObj == null) {
        vm.scope.currentPage = 1;
        summaryObj = {"currentPage":vm.scope.currentPage, "itemsPerPage":vm.scope.itemsPerPage};
      }

      var params = {
        'pageItems': vm.scope.itemsPerPage,
        'pageIndex': summaryObj.currentPage
      };

      if(vm.scope.searchKeyword) {
        if(flag == 'name') {
          params['zoneName'] = vm.scope.searchKeyword;
        }
      }

      paasContainerService.containerSummary(params).then(
        function(result) {
          vm.containerSummary = result.data.data;

          vm.scope.totalItems = result.data.totalCount;
          vm.scope.pageItems = result.data.pageItems;
          vm.scope.totalPages = Math.ceil(result.data.totalCount / result.data.pageItems);

          if(vm.containerSummary) {
            vm.scope.containerRunning = result.data.overview.running;
            vm.scope.containerCritical = result.data.overview.critical;
            vm.scope.containerWarning = result.data.overview.warning;
            vm.scope.containerTotal = result.data.overview.total;

            vm.getCellContainerRelationInfo(vm.containerSummary[0]);
          } else {
            vm.selectedPaasta = null;
            vm.topProcessMemoryList = null;
            vm.scope.loading = false;
          }

          vm.scope.loadingContainerSummary = false;
        },
        function(reason) {
          vm.scope.loadingContainerSummary = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();

    // Page Change
    vm.scope.pageChanged = function() {
      var summaryObj = {"currentPage":vm.scope.currentPage};
      vm.getCellContainerRelationInfo(summaryObj);
    };


    // Cell & Container Relationship
    var oldObj = {};
    vm.scope.doing_async = true;

    vm.getCellContainerRelationInfo = function(obj) {
      oldObj['select'] = '';
      obj['select'] = 'active';
      vm.selectedContainer = $sce.trustAsHtml('[ <span style="color:#77ae33;">' + obj.zoneName + '</span> ]');

      paasContainerService.containerRelationship(obj.zoneName).then(
        function(result) {
          vm.cellSummary = result.data;
          vm.relationshipList = result.data;
          vm.scope.loadingRelationship = false;
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loadingRelationship = false;
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
      oldObj = obj;
    };


    // Tile Color
    vm.getTileColor = function(status) {
      var tileClass = '';
      switch(status) {
        case 'fail' : tileClass = 'tile-fail'; break;
        case 'warning' : tileClass = 'tile-warning'; break;
        case 'critical' : tileClass = 'tile-critical'; break;
        default: tileClass = 'tile'; break;
      }
      return tileClass;
    };

    // Go Container Detail
    vm.goContainerDetail = function(container) {
      if(container.status != 'fail') {
        $location.path('/paas/container/'+container.containerId).search({'name' : container.appName, 'appIndex' : container.appIndex});
      }
    };


    // Cell Overview List Pop
    vm.getCellOverviewList = function(item, $event) {
      if(item.id == 'running' || item.id == 'total') {
        $event.stopPropagation();
        return;
      }

      vm.cellOverviewPopTitle = item.name;
      // vm.cellOverviewListPop = false;
      vm.cellOverviewNullPop = false;
      vm.scope.loadingStatusListPop = true;

      paasContainerService.cellOverviewList(item.id).then(
        function(result) {
          vm.cellOverviewListPop = result.data;
          if(result.data == null) {
            vm.cellOverviewNullPop = 'No Data Available.';
          }
          vm.scope.loadingOverviewListPop = false;
        },
        function(reason) {
          vm.scope.loadingOverviewListPop = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Go Cell Detail View
    vm.goCellDetailView = function(item) {
      angular.element('#cellOverviewList').modal('hide');
      var close = $interval(function() {
        if(item != null && item.status != 'fail') {
          if(angular.element('#cellOverviewList').hasClass('in') == false) {
            $interval.cancel(close);
            $location.path('/paas/paasta/' + item.cellId);
          }
        }
      }, 300);
    };


    // Container Overview List Pop
    vm.getContainerOverviewList = function(status, title, $event) {
      if(status == 'running' || status == 'total') {
        $event.stopPropagation();
        return;
      }

      vm.containerOverviewPopTitle = title;
      vm.containerOverviewListPop = false;
      vm.containerOverviewNullPop = false;
      vm.scope.loadingStatusListPop = true;

      paasContainerService.containerOverviewList(status).then(
        function(result) {
          vm.containerOverviewListPop = result.data;
          if(result.data == null) {
            vm.containerOverviewNullPop = 'No Data Available.';
          }
          vm.scope.loadingOverviewListPop = false;
        },
        function(reason) {
          vm.scope.loadingOverviewListPop = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Go Container Detail View
    vm.goContainerDetailView = function(item) {
      angular.element('#containerOverviewList').modal('hide');
      var close = $interval(function() {
        if(item != null && item.status != 'fail') {
          if(angular.element('#containerOverviewList').hasClass('in') == false) {
            $interval.cancel(close);
            $location.path('/paas/contatiner/' + item.containerId).search('addr', item.ip);
          }
        }
      }, 300);
    };



    // Table Value Color Change
    var cpu = {};
    var memory = {};
    var disk = {};
    vm.scope.cpuUsageStyle = function(value) {
      var style = {};
      if(cpu.critical <= value) {
        style = {color: 'red'};
      } else if(cpu.warning <= value && value < cpu.critical) {
        style = {color: 'orange'};
      }
      return style;
    };
    vm.scope.memoryUsageStyle = function(value) {
      var style = {};
      if(memory.critical <= value) {
        style = {color: 'red'};
      } else if(memory.warning <= value && value < memory.critical) {
        style = {color: 'orange'};
      }
      return style;
    };
    vm.scope.diskUsageStyle = function(value) {
      var style = {};
      if(disk.critical <= value) {
        style = {color: 'red'};
      } else if(disk.warning <= value && value < disk.critical) {
        style = {color: 'orange'};
      }
      return style;
    };
    vm.scope.textStatusStyle = function(value) {
      var style = {};
      if('fail' == value) {
        style = {color: '#de4d58'};
      } else if('warning' == value) {
        style = {color: '#fbae42'};
      } else if('critical' == value) {
        style = {color: '#a76dd8'};
      }
      return style;
    };


    vm.scope.errStateStyle = function(value) {
      var style = {};
      if('fail' == value) {
        style = {color: '#de4d58'};
      } else if('warning' == value) {
        style = {color: '#fbae42'};
      } else if('critical' == value) {
        style = {color: '#a76dd8'};
      } else {
        style = {color: '#8a8c99'};
      }
      return style;
    };


    // Reload
    vm.scope.$on('broadcast:reload', function() {
      vm.scope.selSearchCondition = 'name';
      vm.scope.searchKeyword = '';

      vm.scope.loadingCellOverview = true;
      vm.scope.loadingContainerOverview = true;
      vm.scope.loadingContainerSummary = true;
      vm.scope.loadingCellSummary = true;
      vm.scope.loadingRelationship = true;
      vm.scope.loading = true;

      vm.getCellOverview();
      vm.getContainerOverview();
      vm.getContainerSummary(null, '');
    });

  }


  angular
    .module('monitoring')
    .controller('PaasContainerDetailController', PaasContainerDetailController);

  /** @ngInject */
  function PaasContainerDetailController($scope, $log, $stateParams, $interval, $timeout, $location, $exceptionHandler
                                      , paasContainerDetailService, containerChartConfig
                                      , common, cookies, nvd3Generator) {
    var vm = this;
    vm.scope = $scope;
    vm.Math = Math;

    vm.scope.loading = true;

    vm.scope.name = $location.search().name;
    vm.scope.appIndex = $location.search().appIndex;

    // Top Tab Change
    vm.scope.selTab = function(tab) {
      vm.scope.selectedTab = tab;
      if (tab == 'chart') {
        /*if(widgets.length > 0) {
          $scope.dashboard.widgets = widgets;
          $timeout(function() {
            $scope.$broadcast('resize');
          }, 200);
        }*/
      } else if (tab == 'logs') {
        if (vm.scope.recentLogs == null) {
          vm.scope.defaultRecentLog();
        }
      }
    };

    // Gridster (Layout)
    vm.scope.gridsterOpts = {
      margins: [20, 20],
      columns: 3,
      rowHeight: 250,
      mobileModeEnabled: false,
      swapping: true,
      draggable: {
        handle: 'h4',
        stop: function (event, $element, widget) {
          $timeout(function () {
            $log.log(event + ',' + $element + ',' + widget);
          }, 400)
        }
      },
      resizable: {
        enabled: true,
        handles: ['n', 'e', 's', 'w', 'ne', 'se', 'sw', 'nw'],
        minWidth: 200,
        layoutChanged: function () {
        },

        // optional callback fired when resize is started
        start: function (event, $element, widget) {
          $timeout(function () {
            $log.log(event + ',' + $element + ',' + widget);
          }, 400)
        },

        // optional callback fired when item is resized,
        resize: function (event, $element, widget) {
          if (widget.chart.api) widget.chart.api.update();
        },

        // optional callback fired when item is finished resizing
        stop: function (event, $element, widget) {
          $timeout(function () {
            if (widget.chart.api) widget.chart.api.update();
          }, 400)
        }
      }
    };

    vm.scope.dashboard = {
      widgets: []
    };

    vm.scope.events = {
      resize: function (e, scope) {
        $timeout(function () {
          if (scope.api && scope.api.update) scope.api.update();
        }, 200)
      }
    };

    vm.scope.config = {visible: false};

    $timeout(function () {
      vm.scope.config.visible = true;
    }, 200);

    angular.element(window).on('resize', function () {
      vm.scope.$broadcast('resize');
    });


    // Chart
    var charts = containerChartConfig;

    // Search Condition Setting
    var id = $stateParams.id;
    var condition = {
      id: id,
      groupBy: cookies.getGroupBy()
    };
    if (cookies.getDefaultTimeRange() == 'custom') {
      condition['timeRangeFrom'] = common.timeDifference(new Date().getTime(), cookies.getTimeRangeFrom());
      condition['timeRangeTo'] = common.timeDifference(new Date().getTime(), cookies.getTimeRangeTo());
    } else {
      condition['defaultTimeRange'] = cookies.getDefaultTimeRange();
    }

    // Service Calling & Chart Setting
    var count = 0;
    for (var i in charts) {
      var chartOpt = charts[i];
      if (chartOpt.func) {
        (function (opt, cnt) {
          paasContainerDetailService[opt.func](condition).then(
            function (result) {
              vm.scope.setWidget(cnt, opt, result.data);
            },
            function (reason) {
              vm.scope.setWidget(cnt, opt);
              $log.error(reason);
              $timeout(function () {
                $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message});
              }, 500);
            }
          );
        })(chartOpt, count);
        count++;
      }
    }
    var stop = $interval(function () {
      if (count == (vm.scope.dashboard.widgets).length) {
        $interval.cancel(stop);
        vm.scope.dashboard.widgets.sort(common.CompareForSort);
        vm.scope.loading = false;
      }
    }, 500);

    // Line Chart Create
    vm.scope.setWidget = function (index, dtvOpt, jsonArr) {
      var col = dtvOpt.col == undefined ? Math.floor(index % 3) : dtvOpt.col;
      var row = dtvOpt.row == undefined ? Math.floor(index / 3) : dtvOpt.row;
      var sizeX = dtvOpt.sizeX == undefined ? 1 : dtvOpt.sizeX;
      var sizeY = dtvOpt.sizeY == undefined ? 1 : dtvOpt.sizeY;

      var widget = {
        col: col, row: row, sizeX: sizeX, sizeY: sizeY, name: dtvOpt.name, id: dtvOpt.id, type: dtvOpt.type,
        chart: {
          options: nvd3Generator[dtvOpt.type].options(),
          api: {}
        },
        func: dtvOpt.func
      };

      if(dtvOpt.percent && jsonArr) {
        widget.chart.options.chart.forceY = [0, 100];
        widget.chart.options.chart.yAxis.tickFormat = function (d) {
          return d3.format('.0%')(d / 100);
        };
        widget.percent = dtvOpt.percent;
      }

      if(dtvOpt.axisLabel) {
        widget.chart.options.chart.yAxis.axisLabel = dtvOpt.axisLabel;
        widget.chart.options.chart.yAxis.axisLabelDistance = -5;
        widget.chart.options.chart.margin.left = 55;
        widget.axisLabel = dtvOpt.axisLabel;
      }

      if(jsonArr) {
        var value, arr = [];
        var checkData = 0;
        for (var i in jsonArr) {
          value = jsonArr[i].metric == null ? [{time: 0, usage: 0}] : jsonArr[i].metric;
          arr.push({values: value, key: jsonArr[i].name});
          for (var j in value) {
            if (value[j] != null) {
              if (checkData < value[j].usage) {
                checkData = value[j].usage;
              }
            }
          }
        }
        widget.chart.data = arr;

        if (dtvOpt.type != 'list') {
          if (checkData < 5 && widget.chart.options.chart.forceY == undefined) {
            widget.chart.options.chart.forceY = [0, 5];
          }
          if (checkData > 10000) {
            widget.chart.options.chart.yAxis.axisLabelDistance = 20;
            widget.chart.options.chart.margin.left = 80;
          }
        }
      } else {
        widget.chart.options.chart.forceY = false;
      }

      if (cookies.getDefaultTimeRange() == '7d' || cookies.getDefaultTimeRange() == '30d') {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
        };
      } else if (cookies.getDefaultTimeRange() == 'custom' && (cookies.getGroupBy() == '11h12m' || cookies.getGroupBy() == '48h')) {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
        };
      } else {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%H:%M:%S')(new Date(d * 1000));
        };
      }

      vm.scope.dashboard.widgets.push(widget);
    };

    //   GroupBy 
    var savedCustom = false;
    vm.scope.saveTimeRange = function () {
      var condition = {
        id: id,
        groupBy: cookies.getGroupBy()
      };

      if (vm.scope.selTimeRange == 'custom') {
        condition['timeRangeFrom'] = common.timeDifference(new Date().getTime(), vm.scope.timeRangeFrom);
        condition['timeRangeTo'] = common.timeDifference(new Date().getTime(), vm.scope.timeRangeTo);
        savedCustom = true;
      } else {
        condition['defaultTimeRange'] = vm.scope.selTimeRange;
        condition.defaultTimeRange = vm.scope.selTimeRange;
        condition.groupBy = vm.scope.selGroupBy;
        savedCustom = false;
      }
      cookies.putGroupBy(vm.scope.selGroupBy);

      angular.forEach(vm.scope.dashboard.widgets, function (widget, index) {
        (function (opt, idx) {
          paasContainerDetailService[opt.func](condition).then(
            function (result) {
              if (result) {
                if (cookies.getDefaultTimeRange() == '7d' || cookies.getDefaultTimeRange() == '30d') {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
                  };
                } else if (cookies.getDefaultTimeRange() == 'custom' && (cookies.getGroupBy() == '11h12m' || cookies.getGroupBy() == '48h')) {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
                  };
                } else {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%H:%M:%S')(new Date(d * 1000));
                  };
                }

                var jsonArr = result.data;
                var value, arr = [];
                for (var i in jsonArr) {
                  value = jsonArr[i].metric == null ? [{time: 0, usage: 0}] : jsonArr[i].metric;
                  arr.push({values: value, key: jsonArr[i].name});
                }

                vm.scope.dashboard.widgets[idx].chart.data = arr;
                vm.scope.dashboard.widgets[idx].chart.api.refresh();
                vm.scope.dashboard.widgets[idx].loading = false;
              }
            },
            function (reason, status) {
              $timeout(function () {
                $exceptionHandler(reason.Message, {code: status, message: reason.Message});
              }, 500);
            }
          );
        })(widget, index);
      });
    };

    // time range     group by 
    vm.scope.selectGroupBy = function () {
      vm.scope.selGroupBy = common.getGroupingByTimeRange(vm.scope.selTimeRange, vm.scope.timeRangeFrom, vm.scope.timeRangeTo);
    };

    //  save      
    angular.element('#timeRange').on('hidden.bs.modal', function () {
      if (savedCustom != true) {
        vm.scope.selTimeRange = cookies.getDefaultTimeRange();
        vm.scope.selGroupBy = cookies.getGroupBy();
      }
    });


    // Overview Page
    vm.scope.goMovePage = function() {
      $location.path("/paas/container");
    };


    // Reload
    vm.scope.$on('broadcast:reload', function () {
      if (vm.scope.selectedTab == 'logs') {
        vm.scope.logSearch();
      } else {
        angular.forEach(vm.scope.dashboard.widgets, function (widget) {
          widget.loading = true;
        });
        vm.scope.saveTimeRange();
      }
    });
    vm.scope.$on('broadcast:saveTimeRange', function (event, data) {
      vm.scope.selTimeRange = data.selTimeRange;
      vm.scope.selGroupBy = data.selGroupBy;
      vm.scope.selRefreshTime = data.selRefreshTime;
      vm.scope.timeRangeFrom = data.timeRangeFrom;
      vm.scope.timeRangeTo = data.timeRangeTo;
      vm.scope.saveTimeRange();
    });
  }

})();


(function() {
  'use strict';

  angular
    .module('monitoring')
    .factory('paasBoshService', PaasBoshService);

  /** @ngInject */
  function PaasBoshService($http, apiUris, common) {
    var service = {};

    service.boshOverview = function() {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasBoshOverview, config);
    };

    service.boshSummary = function(params) {
      var config = {
        params: params,
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasBoshSummary, config);
    };

    service.boshTopProcessMemory = function(condition) {
      var config = {
        params: condition,
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasBoshTopProcessMemory.replace(":id", condition.id), config);
    };


    service.boshCpuUsageList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshCpuUsageList.replace(":id", condition.id), config);
    };

    service.boshCpuLoadAverageList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshCpuLoadAverageList.replace(":id", condition.id), config);
    };

    service.boshMemoryUsageList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshMemoryUsageLis.replace(":id", condition.id), config);
    };

    service.boshDiskUsageList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshDiskUsageList.replace(":id", condition.id), config);
    };

    service.boshDiskIOList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshDiskIOList.replace(":id", condition.id), config);
    };

    service.boshNetworkIoByteList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshNetworkIoByteList.replace(":id", condition.id), config);
    };

    service.boshNetworkIoPackteList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshNetworkIoPackteList.replace(":id", condition.id), config);
    };

    service.boshNetworkIoDropList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshNetworkIoDropList.replace(":id", condition.id), config);
    };

    service.boshNetworkIoErrorList = function(condition) {
      var config = {
        params: common.setDtvParam(condition),
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasBoshNetworkIoErrorList.replace(":id", condition.id), config);
    };

    return service;
  }

})();

(function() {
  'use strict';

  angular
    .module('monitoring')
    .controller('PaasBoshController', PaasBoshController);

  /** @ngInject */
  function PaasBoshController($scope, $timeout, $sce, $exceptionHandler, common, paasBoshService) {
    var vm = this;
    vm.scope = $scope;
    vm.Math = Math;
    vm.scope.loading = true;
    vm.common = common;

    // Bosh Summary
    (vm.getBoshSummary = function(summaryObj) {
      if(vm.scope.loading == false) {
        vm.scope.loading = true;
      }

      if(summaryObj == undefined || summaryObj == null) {
        vm.scope.currentPage = 1;
        summaryObj = {"currentPage":vm.scope.currentPage, "itemsPerPage":vm.scope.itemsPerPage};
      }

      var params = {
        'pageItems': vm.scope.itemsPerPage,
        'pageIndex': summaryObj.currentPage
      };

      paasBoshService.boshSummary(params).then(
        function(result) {
          vm.boshSummary = result.data.data;

          vm.scope.totalItems = result.data.totalCount;
          vm.scope.totalPages = Math.ceil(result.data.totalCount / result.data.pageItem);

          if(vm.boshSummary) {
            vm.getTopProcessMemoryList(vm.boshSummary[0]);
          } else {
            vm.selectedBosh = null;
            vm.topProcessMemoryList = null;
          }

          vm.scope.loadingSummary = false;
        },
        function(reason) {
          vm.scope.loadingSummary = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();


    // Top Process Memory
    var oldObj = {};
    vm.getTopProcessMemoryList = function(obj) {
      vm.scope.loading = true;
      oldObj['select'] = '';
      obj['select'] = 'active';
      vm.selectedBosh = $sce.trustAsHtml('[ <span style="color:#77ae33;">' + obj.name + '</span> ]');

      var condition = { 'id': obj.id };
      paasBoshService.boshTopProcessMemory(condition).then(
        function(result) {
          vm.topProcessMemoryList = result.data.data;
          vm.scope.loadingTopProcess = false;
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loadingTopProcess = false;
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
      oldObj = obj;
    };


    vm.scope.errStateStyle = function(value) {
      var style = {};
      if('fail' == value) {
        style = {color: '#de4d58'};
      } else if('warning' == value) {
        style = {color: '#fbae42'};
      } else if('critical' == value) {
        style = {color: '#a76dd8'};
      } else {
        style = {color: '#8a8c99'};
      }
      return style;
    };


    // Reload
    vm.scope.$on('broadcast:reload', function() {
      vm.scope.loadingSummary = true;
      vm.scope.loadingTopProcess = true;
      vm.scope.loading = true;

      vm.getBoshSummary();
    });

  }


  angular
    .module('monitoring')
    .controller('PaasBoshDetailController', PaasBoshDetailController);

  /** @ngInject */
  function PaasBoshDetailController($scope, $log, $stateParams, $interval, $timeout, $location, $window, $exceptionHandler
                                  , paasBoshService, paasLogService, boshChartConfig
                                  , common, cookies, nvd3Generator) {
    var vm = this;
    vm.scope = $scope;
    vm.Math = Math;

    vm.scope.loading = true;

    vm.scope.name = $location.search().name;

    // Top Tab Change
    vm.scope.selTab = function(tab) {
      vm.scope.selectedTab = tab;
      if (tab == 'chart') {
        /*if(widgets.length > 0) {
          $scope.dashboard.widgets = widgets;
          $timeout(function() {
            $scope.$broadcast('resize');
          }, 200);
        }*/
      } else if (tab == 'logs') {
        if (vm.scope.recentLogs == null) {
          vm.scope.defaultRecentLog();
        }
      }
    };

    // Gridster (Layout)
    vm.scope.gridsterOpts = {
      margins: [20, 20],
      columns: 3,
      rowHeight: 250,
      mobileModeEnabled: false,
      swapping: true,
      draggable: {
        handle: 'h4',
        stop: function (event, $element, widget) {
          $timeout(function () {
            $log.log(event + ',' + $element + ',' + widget);
          }, 400)
        }
      },
      resizable: {
        enabled: true,
        handles: ['n', 'e', 's', 'w', 'ne', 'se', 'sw', 'nw'],
        minWidth: 200,
        layoutChanged: function () {
        },

        // optional callback fired when resize is started
        start: function (event, $element, widget) {
          $timeout(function () {
            $log.log(event + ',' + $element + ',' + widget);
          }, 400)
        },

        // optional callback fired when item is resized,
        resize: function (event, $element, widget) {
          if (widget.chart.api) widget.chart.api.update();
        },

        // optional callback fired when item is finished resizing
        stop: function (event, $element, widget) {
          $timeout(function () {
            if (widget.chart.api) widget.chart.api.update();
          }, 400)
        }
      }
    };

    vm.scope.dashboard = {
      widgets: []
    };

    vm.scope.events = {
      resize: function (e, scope) {
        $timeout(function () {
          if (scope.api && scope.api.update) scope.api.update();
        }, 200)
      }
    };

    vm.scope.config = {visible: false};

    $timeout(function () {
      vm.scope.config.visible = true;
    }, 200);

    angular.element(window).on('resize', function () {
      vm.scope.$broadcast('resize');
    });

    // Chart
    var charts = boshChartConfig;

    // Search Condition Setting
    var id = $stateParams.id;
    var condition = {
      id: id,
      groupBy: cookies.getGroupBy()
    };
    if (cookies.getDefaultTimeRange() == 'custom') {
      condition['timeRangeFrom'] = common.timeDifference(new Date().getTime(), cookies.getTimeRangeFrom());
      condition['timeRangeTo'] = common.timeDifference(new Date().getTime(), cookies.getTimeRangeTo());
    } else {
      condition['defaultTimeRange'] = cookies.getDefaultTimeRange();
    }

    // Service Calling & Chart Setting
    var count = 0;
    for (var i in charts) {
      var chartOpt = charts[i];
      if (chartOpt.func) {
        (function (opt, cnt) {
          paasBoshService[opt.func](condition).then(
            function (result) {
              vm.scope.setWidget(cnt, opt, result.data);
            },
            function (reason) {
              vm.scope.setWidget(cnt, opt);
              $log.error(reason);
              $timeout(function () {
                $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message});
              }, 500);
            }
          );
        })(chartOpt, count);
        count++;
      }
    }
    var stop = $interval(function () {
      if (count == (vm.scope.dashboard.widgets).length) {
        $interval.cancel(stop);
        vm.scope.dashboard.widgets.sort(common.CompareForSort);
        vm.scope.loading = false;
      }
    }, 500);

    // Line Chart Create
    vm.scope.setWidget = function (index, dtvOpt, jsonArr) {
      var col = dtvOpt.col == undefined ? Math.floor(index % 3) : dtvOpt.col;
      var row = dtvOpt.row == undefined ? Math.floor(index / 3) : dtvOpt.row;
      var sizeX = dtvOpt.sizeX == undefined ? 1 : dtvOpt.sizeX;
      var sizeY = dtvOpt.sizeY == undefined ? 1 : dtvOpt.sizeY;

      var widget = {
        col: col, row: row, sizeX: sizeX, sizeY: sizeY, name: dtvOpt.name, id: dtvOpt.id, type: dtvOpt.type,
        chart: {
          options: nvd3Generator[dtvOpt.type].options(),
          api: {}
        },
        func: dtvOpt.func
      };

      if(dtvOpt.percent && jsonArr) {
        widget.chart.options.chart.forceY = [0, 100];
        widget.chart.options.chart.yAxis.tickFormat = function (d) {
          return d3.format('.0%')(d / 100);
        };
        widget.percent = dtvOpt.percent;
      }

      if(dtvOpt.axisLabel) {
        widget.chart.options.chart.yAxis.axisLabel = dtvOpt.axisLabel;
        widget.chart.options.chart.yAxis.axisLabelDistance = -5;
        widget.chart.options.chart.margin.left = 55;
        widget.axisLabel = dtvOpt.axisLabel;
      }

      if(jsonArr) {
        var value, arr = [];
        var checkData = 0;
        for (var i in jsonArr) {
          value = jsonArr[i].metric == null ? [{time: 0, usage: 0}] : jsonArr[i].metric;
          arr.push({values: value, key: jsonArr[i].name});
          for (var j in value) {
            if (value[j] != null) {
              if (checkData < value[j].usage) {
                checkData = value[j].usage;
              }
            }
          }
        }
        widget.chart.data = arr;

        if (dtvOpt.type != 'list') {
          if (checkData < 5 && widget.chart.options.chart.forceY == undefined) {
            widget.chart.options.chart.forceY = [0, 5];
          }
          if (checkData > 10000) {
            widget.chart.options.chart.yAxis.axisLabelDistance = 20;
            widget.chart.options.chart.margin.left = 80;
          }
        }
      } else {
        widget.chart.options.chart.forceY = false;
      }

      if (cookies.getDefaultTimeRange() == '7d' || cookies.getDefaultTimeRange() == '30d') {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
        };
      } else if (cookies.getDefaultTimeRange() == 'custom' && (cookies.getGroupBy() == '1h36m' || cookies.getGroupBy() == '11h12m' || cookies.getGroupBy() == '48h')) {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
        };
      } else {
        widget.chart.options.chart.xAxis.tickFormat = function (d) {
          return d3.time.format('%H:%M:%S')(new Date(d * 1000));
        };
      }

      vm.scope.dashboard.widgets.push(widget);
    };

    //   GroupBy 
    var savedCustom = false;
    vm.scope.saveTimeRange = function () {
      var condition = {
        id: id,
        groupBy: cookies.getGroupBy()
      };

      if (vm.scope.selTimeRange == 'custom') {
        condition['timeRangeFrom'] = common.timeDifference(new Date().getTime(), vm.scope.timeRangeFrom);
        condition['timeRangeTo'] = common.timeDifference(new Date().getTime(), vm.scope.timeRangeTo);
        savedCustom = true;
      } else {
        condition['defaultTimeRange'] = vm.scope.selTimeRange;
        condition.defaultTimeRange = vm.scope.selTimeRange;
        condition.groupBy = vm.scope.selGroupBy;
        savedCustom = false;
      }
      cookies.putGroupBy(vm.scope.selGroupBy);

      angular.forEach(vm.scope.dashboard.widgets, function (widget, index) {
        (function (opt, idx) {
          paasBoshService[opt.func](condition).then(
            function (result) {
              if (result) {
                if (cookies.getDefaultTimeRange() == '7d' || cookies.getDefaultTimeRange() == '30d') {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
                  };
                } else if (cookies.getDefaultTimeRange() == 'custom' && (cookies.getGroupBy() == '1h36m' || cookies.getGroupBy() == '11h12m' || cookies.getGroupBy() == '48h')) {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%y-%m-%d %H:%M:%S')(new Date(d * 1000));
                  };
                } else {
                  vm.scope.dashboard.widgets[idx].chart.options.chart.xAxis.tickFormat = function (d) {
                    return d3.time.format('%H:%M:%S')(new Date(d * 1000));
                  };
                }

                var jsonArr = result.data;
                var value, arr = [];
                for (var i in jsonArr) {
                  value = jsonArr[i].metric == null ? [{time: 0, usage: 0}] : jsonArr[i].metric;
                  arr.push({values: value, key: jsonArr[i].name});
                }

                vm.scope.dashboard.widgets[idx].chart.data = arr;
                vm.scope.dashboard.widgets[idx].chart.api.refresh();
                vm.scope.dashboard.widgets[idx].loading = false;
              }
            },
            function (reason, status) {
              $timeout(function () {
                $exceptionHandler(reason.Message, {code: status, message: reason.Message});
              }, 500);
            }
          );
        })(widget, index);
      });
    };

    // time range     group by 
    vm.scope.selectGroupBy = function () {
      vm.scope.selGroupBy = common.getGroupingByTimeRange(vm.scope.selTimeRange, vm.scope.timeRangeFrom, vm.scope.timeRangeTo);
    };

    //  save      
    angular.element('#timeRange').on('hidden.bs.modal', function () {
      if (savedCustom != true) {
        vm.scope.selTimeRange = cookies.getDefaultTimeRange();
        vm.scope.selGroupBy = cookies.getGroupBy();
      }
    });


    // Log
    vm.scope.targetDateOptions = {format: 'YYYY.MM.DD'};
    vm.scope.timeOptions = {format: 'LT'};

    // Pagination
    vm.scope.totalItems = 0;
    vm.scope.currentPage = 1;
    vm.scope.maxSize = 10;
    vm.scope.pageItems = 50;

    //   
    vm.scope.defaultRecentLog = function (message, optParam) {
      vm.scope.loading = true;
      var param = {
        'id': id,
        'pageItems': vm.scope.pageItems,
        'pageIndex': vm.scope.currentPage,
        'logType': 'bosh',
        'period': '5m'
      };
      if (message) param['keyword'] = message;
      if (optParam) {
        for (var key in optParam) {
          if (optParam.hasOwnProperty(key)) {
            param[key] = optParam[key];
          }
        }
      }
      paasLogService.dtvDefaultRecentLog(param).then(
        function (result) {
          vm.scope.recentLogs = result.data.messages;

          vm.scope.optLogstashIndex = result.data.logstashIndex;
          vm.scope.startTime = result.data.startTime;
          vm.scope.endTime = result.data.endTime;
          vm.scope.targetDate = common.getDate(result.data.targetDate);
          vm.scope.totalItems = result.data.totalCount;
          vm.scope.loading = false;
        },
        function (reason) {
          vm.scope.loading = false;
          $timeout(function () {
            $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message});
          }, 500);
        }
      );
    };


    // Specific Time Range Log
    vm.scope.specificTimeRangeLog = function (message, optParam) {
      var param = {
        'id': id,
        'pageItems': vm.scope.pageItems,
        'pageIndex': vm.scope.currentPage,
        'logType': 'bosh',
        'period': '5m'
      };
      if (message) param['keyword'] = message;
      if (optParam) {
        for (var key in optParam) {
          if (optParam.hasOwnProperty(key)) {
            param[key] = optParam[key];
          }
        }
      }

      paasLogService.dtvSpecificTimeRangeLog(param).then(
        function (result) {
          vm.scope.recentLogs = result.data.messages;

          vm.scope.optLogstashIndex = result.data.logstashIndex;
          vm.scope.startTime = result.data.startTime;
          vm.scope.endTime = result.data.endTime;
          vm.scope.totalItems = result.data.totalCount;
          vm.scope.loading = false;
        },
        function (reason) {
          vm.scope.loading = false;
          $timeout(function () {
            $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message});
          }, 500);
        }
      );
    };


    var getType = 'default';
    var message = '';
    vm.scope.logSearch = function () {
      vm.scope.loading = true;
      vm.scope.currentPage = 1;
      message = vm.scope.message;

      if(vm.scope.targetDate) {
        getType = 'specific';
        vm.scope.startTime == undefined ? vm.scope.optStartTime = '' : vm.scope.optStartTime = common.getTime(vm.scope.startTime);
        vm.scope.endTime == undefined ? vm.scope.optEndTime = '' : vm.scope.optEndTime = common.getTime(vm.scope.endTime);
        var optParam = {
          'targetDate': common.getDate(vm.scope.targetDate),
          'startTime': vm.scope.optStartTime,
          'endTime': vm.scope.optEndTime
        };
        vm.scope.specificTimeRangeLog(message, optParam);
      } else {
        getType = 'default';
        vm.scope.defaultRecentLog(message);
      }
    };


    // Page Change
    vm.scope.pageChanged = function () {
      vm.scope.loading = true;
      var optParam = {
        'logstashIndex': vm.scope.optLogstashIndex,
        'startTime': vm.scope.optStartTime,
        'endTime': vm.scope.optEndTime
      };
      if(getType == 'specific') {
        optParam['targetDate'] = common.getDate(vm.scope.targetDate);
        vm.scope.specificTimeRangeLog(message, optParam);
      } else {
        vm.scope.defaultRecentLog(message, optParam);
      }

      $window.scrollTo(0, 0);
    };


    // Search Time Reset
    vm.scope.searchReset = function() {
      vm.scope.targetDate = '';
      vm.scope.startTime = '';
      vm.scope.endTime = '';
    }


    // Overview Page
    vm.scope.goMovePage = function() {
      $location.path("/paas/bosh");
    };


    // Reload
    vm.scope.$on('broadcast:reload', function () {
      if (vm.scope.selectedTab == 'logs') {
        vm.scope.logSearch();
      } else {
        angular.forEach(vm.scope.dashboard.widgets, function (widget) {
          widget.loading = true;
        });
        vm.scope.saveTimeRange();
      }
    });
    vm.scope.$on('broadcast:saveTimeRange', function (event, data) {
      vm.scope.selTimeRange = data.selTimeRange;
      vm.scope.selGroupBy = data.selGroupBy;
      vm.scope.selRefreshTime = data.selRefreshTime;
      vm.scope.timeRangeFrom = data.timeRangeFrom;
      vm.scope.timeRangeTo = data.timeRangeTo;
      vm.scope.saveTimeRange();
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('monitoring')
    .factory('Excel', Excel);

  function Excel($window, $document) {
    var uri = 'data:application/vnd.ms-excel;base64,',
      template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">' +
        '<head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head>' +
        '<body>{table}</body></html>',
      base64 = function(s){return $window.btoa(unescape(encodeURIComponent(s)));},
      format = function(s,c){return s.replace(/{(\w+)}/g,function(m,p){return c[p];})};
    return {
      tableToExcel : function(tableId, worksheetName) {
        var table = $document[0].getElementById(tableId);
        var ctx = {
            worksheet : worksheetName,
            table : table.innerHTML
          };
        var href = uri + base64(format(template, ctx));
        return href;
      }
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('monitoring')
    .factory('paasAlarmPolicyService', paasAlarmPolicyService);

  /** @ngInject */
  function paasAlarmPolicyService($http, apiUris) {
    var service = {};

    /**********  **********/
    service.alarmPolicyList = function() {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmPolicyList, config);
    };

    service.alarmSnsChannelList = function() {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmSnsChannelList, config);
    };

    service.channelRegist = function (data) {
      var config = {
        headers : {'Accept': 'application/json'}
      };
      return $http.post(apiUris.paasAlarmSnsChannelRegist, data, config);
    };

    service.channelDelete = function (id) {
      var config = {
        headers : {'Accept': 'application/json'}
      };
      return $http.delete(apiUris.paasAlarmSnsChannelDelete.replace(":id", id), config);
    };

    service.updateAlarmSetup = function(data) {
      var config = {
        headers : {'Content-Type': 'application/json', 'Accept': 'application/json'}
      };
      return $http.put(apiUris.paasAlarmPolicy, data, config);
    };

    return service;
  }


  angular
    .module('monitoring')
    .factory('paasAlarmStatusService', paasAlarmStatusService);

  /** @ngInject */
  function paasAlarmStatusService($http, apiUris) {
    var service = {};

    service.alarmStatusList = function(params) {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatusList + params, config);
    };

    service.alarmStatusCount = function(params) {
      var config = {
        params: params,
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatusCount, config);
    };

    service.alarmStatusDetail = function(id) {
      var config = {
        headers : {'Accept' : 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatusId.replace(":id", id), config);
    };

    service.alarmStatusUpdate = function(id, data) {
      var config = {
        headers : {'Content-Type': 'application/json', 'Accept': 'application/json'}
      };
      return $http.put(apiUris.paasAlarmStatusId.replace(":id", id), data, config);
    };

    service.alarmActionCreate = function(data){
      var config = {
        headers : {'Accept' : 'application/json'}
      };
      return $http.post(apiUris.paasAlarmAction, data, config);
    };

    service.alarmActionUpdate = function(actionId, data){
      var config = {
        headers : {'Content-Type': 'application/json', 'Accept': 'application/json'}
      };
      return $http.patch(apiUris.paasAlarmActionId.replace(":actionId", actionId), data, config);
    };

    service.alarmActionDelete = function(actionId){
      var config = {
        headers : {'Accept' : 'application/json'}
      };
      return $http.delete(apiUris.paasAlarmActionId.replace(":actionId", actionId), config);
    };

    return service;
  }


  angular
    .module('monitoring')
    .factory('paasAlarmStatisticsService', paasAlarmStatisticsService);

  /** @ngInject */
  function paasAlarmStatisticsService($http, apiUris) {
    var service = {};

    service.alarmStatistics = function(params) {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatisticList + params, config);
    };

    service.alarmStatisticTotal = function(params) {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatisticTotal + params, config);
    };

    service.alarmStatisticService = function(params) {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatisticService + params, config);
    };

    service.alarmStatisticMatrix = function(params) {
      var config = {
        headers: {'Accept': 'application/json'}
      };
      return $http.get(apiUris.paasAlarmStatisticMatrix + params, config);
    };





    return service;
  }

})();


(function() {
  'use strict';

  angular
    .module('monitoring')
    .controller('PaasAlarmPolicyController', PaasAlarmPolicyController);

  /** @ngInject */
  function PaasAlarmPolicyController($scope, $timeout, $document, $window, $exceptionHandler, paasAlarmPolicyService) {
    var vm = this;
    vm.scope = $scope;

    vm.scope.loading = true;

    (vm.getAlarmPolicyList = function() {
      paasAlarmPolicyService.alarmPolicyList().then(
        function(result) {

          var setup = {
            pas: {
              cpu: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              memory: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              disk: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              measureTime : 0,
              email: {emailName: '', emailAddr: '', emailType: '', emailSendYn: true}
            },
            bos: {
              cpu: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              memory: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              disk: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              email: {emailName: '', emailAddr: '', emailType: '', emailSendYn: true}
            },
            con: {
              cpu: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              memory: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              disk: {threshold: {critical: 0, warning: 0}, repeatTime: 0},
              email: {emailName: '', emailAddr: '', emailType: '', emailSendYn: true}
            }
          };

          for(var i in result.data) {
            setup[result.data[i].originType][result.data[i].alarmType] = {
              threshold: {
                critical: result.data[i].criticalThreshold,
                warning: result.data[i].warningThreshold
              },
              repeatTime: result.data[i].repeatTime
            };

            // 
            setup[result.data[i].originType].measureTime = result.data[i].measureTime;

            if(result.data[i].mailAddress != null && result.data[i].mailAddress != "") {
              if(result.data[i].mailAddress.indexOf("@") > -1) {
                var array = result.data[i].mailAddress.split('@');
                var vBool = true;
                var vType = array[1];
                for(var j = 0; j < vm.mailList.length; j++) {
                  if(vm.mailList[j].code == vType) { vBool = false; }
                }
                if(vBool) { vType = ''; }
                setup[result.data[i].originType].email = {
                  emailName: array[0],
                  emailAddr: array[1],
                  emailType: vType
                }
              }
            }

            //   
            if(result.data[i].mailSendYn == "Y") {
              setup[result.data[i].originType].email.emailSendYn = true;
            } else {
              setup[result.data[i].originType].email.emailSendYn = false;
            }
          }

          vm.setup = setup;
          vm.selType = vm.selType == null ? 'bos' : vm.selType;
          vm.setTypeObject();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();


    // Alarm Channel List
    vm.snsSendYn = false;
    (vm.getAlarmSnsChannelList = function() {
      paasAlarmPolicyService.alarmSnsChannelList().then(
        function(result) {
          if(result.data != '' && result.data != null) {
            vm.alarmSnsChannelList = result.data;
            if(result.data[0].snsSendYn == "Y") {
              vm.snsSendYn = true;
            }
          } else {
            angular.element('#snsChannelList').empty();
            vm.alarmSnsChannelList = null;
          }
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();


    vm.channel = {
      type : '',
      id : '',
      token : '',
      description : ''
    }

    // Sns Channel Regist
    vm.channelRegist = function () {
      vm.scope.loading = true;

      if(!(vm.channel.type != '' && vm.channel.id && vm.channel.token)) {
        vm.scope.loading = false;
        var vMsg = '';
        if (vm.channel.type == '') {
          vMsg = 'Please Select Channel Type';
        } else if (vm.channel.id == '') {
          vMsg = 'Please Enter Channel ID';
        } else if (vm.channel.token == '') {
          vMsg = 'Please Enter Token Value ';
        }
        $exceptionHandler(vMsg, {code: status, message: vMsg});
        return false;
      }

      var snsSendYn = "Y";
      if(!vm.snsSendYn) { snsSendYn = "N"; }

      var body = {
        originType : vm.channel.type,
        snsId : vm.channel.id,
        token : vm.channel.token,
        expl : vm.channel.description,
        sendSnsYn : snsSendYn
      }

      paasAlarmPolicyService.channelRegist(body).then(
        function(result) {
          if(result.data.status == 'Created') {
            vm.channel.type = '';
            vm.channel.id = '';
            vm.channel.token = '';
            vm.channel.description = '';
          }
          vm.getAlarmSnsChannelList();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data, {code: 500, message: reason.data}); });
        }
      );
    };

    // Sns Channel Delete
    vm.channelDelete = function(id) {
      paasAlarmPolicyService.channelDelete(id).then(
        function() {
          vm.getAlarmSnsChannelList();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data, {code: 500, message: reason.data}); });
        }
      );
    };

    // Policy Save
    vm.saveSetup = function() {
      vm.scope.loading = true;

      if(!(vm.origin.email.emailName != '' && vm.origin.email.emailAddr != '') && vm.origin.email.emailSendYn == 'Y') {
        vm.scope.loading = false;
        var vMsg = '';
        if (vm.origin.email.emailName == '') {
          vMsg = 'Please enter Email Name';
        } else {
          vMsg = 'Please enter Email Addr or selete Email Addr ';
        }
        $exceptionHandler(vMsg, {code: status, message: vMsg});
      }

      var mailSendYn = "Y";
      if(!vm.origin.email.emailSendYn) { mailSendYn = "N"; }

      var snsSendYn = "Y";
      if(!vm.snsSendYn) { snsSendYn = "N"; }

      var body = [
        {
          originType: vm.selType,
          alarmType: "cpu",
          warningThreshold: vm.origin.cpu.threshold.warning,
          criticalThreshold: vm.origin.cpu.threshold.critical,
          repeatTime: vm.origin.cpu.repeatTime,
          measureTime: vm.origin.measureTime
        },
        {
          originType: vm.selType,
          alarmType: "memory",
          warningThreshold: vm.origin.memory.threshold.warning,
          criticalThreshold: vm.origin.memory.threshold.critical,
          repeatTime: vm.origin.memory.repeatTime,
          measureTime: vm.origin.measureTime
        },
        {
          originType: vm.selType,
          alarmType: "disk",
          warningThreshold: vm.origin.disk.threshold.warning,
          criticalThreshold: vm.origin.disk.threshold.critical,
          repeatTime: vm.origin.disk.repeatTime,
          measureTime: vm.origin.measureTime
        },
        {
          originType: vm.selType,
          mailAddress: vm.origin.email.emailName + '@' + vm.origin.email.emailAddr,
          mailSendYn: mailSendYn,
          snsSendYn: snsSendYn
        }
      ];

      paasAlarmPolicyService.updateAlarmSetup(body).then(
        function() {
          vm.getAlarmPolicyList();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data, {code: 500, message: reason.data}); });
        }
      );
    };

    vm.mailList = [
      {name:"gmail",    code:"gmail.com",   $hashKey:"gmail.com"},
      {name:"yahoo",    code:"yahoo.com",   $hashKey:"yahoo.com"},
      {name:"hotmail",  code:"hotmail.com", $hashKey:"hotmail.com"},
      {name:"naver",    code:"naver.com",   $hashKey:"naver.com"},
      {name:"nate",     code:"nate.com",    $hashKey:"nate.com"},
      {name:"daum",     code:"hanmail.net", $hashKey:"hanmail.net"}
    ];

    vm.setEmailAddr = function() {
      if(vm.origin.email.emailType == '') {
        vm.origin.email.emailAddr = '';
      } else {
        vm.origin.email.emailAddr = vm.origin.email.emailType;
      }
    };

    vm.setTypeObject = function() {
      if(vm.selType == 'pas') {
        vm.origin = vm.setup.pas;
      } else if(vm.selType == 'bos') {
        vm.origin = vm.setup.bos;
      } else if(vm.selType == 'con') {
        vm.origin = vm.setup.con;
      }
    };

    // SNS Regist Method
    vm.snsRegistMethod = function() {
      var innerContents = $document[0].getElementById('snsRegistMethod').innerHTML;
      var popupWinindow = $window.open('', '_blank', 'width=724,height=700,scrollbars=no,menubar=no,toolbar=no,location=no,status=no,titlebar=no');
      popupWinindow.document.open();
      popupWinindow.document.write(
        '<html >' +
        '<head>' +
        '<link rel="stylesheet" type="text/css" href="bower_components/components-font-awesome/css/font-awesome.css">'+
        '<link rel="stylesheet" type="text/css" href="styles/vendor-min.css">' +
        '<link rel="stylesheet" type="text/css" href="styles/app-min.css">' +
        '<style>\n' +
        '.contents {font-size:14px;}\n' +
        '.arrow {text-align:center;margin-bottom:15px;}\n' +
        '.thumbnail {padding:15px;margin-bottom:20px;border:1px solid #ddd;border-radius:5px;}\n' +
        '.fa.fa-caret-right {margin-right:5px;}\n' +
        '</style>' +
        '</head>' +
        '<body>' + innerContents + '</body>' +
        '</html>'
      );
      popupWinindow.document.close();
    };

    /********** reload **********/
    vm.scope.$on('broadcast:reload', function() {
      vm.scope.loading = true;
      vm.getAlarmPolicyList();
    });
  }


  angular
    .module('monitoring')
    .controller('PaasAlarmStatusController', PaasAlarmStatusController);

  /** @ngInject */
  function PaasAlarmStatusController($scope, $timeout, $location, $exceptionHandler, common, paasAlarmStatusService) {
    var vm = this;
    vm.scope = $scope;
    vm.scope.loading = true;
    vm.common = common;

    vm.scope.optionsFrom = {format: 'YYYY.MM.DD'};
    vm.scope.optionsTo = {format: 'YYYY.MM.DD'};

    // pagination
    vm.scope.currentPage = 1;             // 
    vm.scope.itemsPerPage = 10;           //   
    vm.scope.maxSize = 10;

    (vm.getAlarmStatusList = function(cPage) {
      vm.stateParams = $location.search();

      if(vm.scope.loading == false) {
        vm.scope.loading = true;
      }

      if(cPage == undefined || cPage == null) {
        vm.scope.currentPage = 1;
      }

      if(vm.stateParams._o) {
        vm.scope.selOriginType = vm.stateParams._o;
      }
      if(vm.stateParams._a) {
        vm.scope.selAlarmType = vm.stateParams._a;
      }
      if(vm.stateParams._l) {
        vm.scope.selLevel = vm.stateParams._l;
      }
      if(vm.stateParams._r) {
        vm.scope.selResolveStatus = vm.stateParams._r;
      }
      if(vm.stateParams._f && vm.stateParams._t) {
        vm.scope.dateFrom = moment(parseInt(vm.stateParams._f));
        vm.scope.dateTo = moment(parseInt(vm.stateParams._t));
      }
      if(vm.stateParams._p) {
        vm.scope.currentPage = vm.stateParams._p;
      }

      var param = '?pageItems=' + vm.scope.itemsPerPage;
          param += '&pageIndex=' + vm.scope.currentPage;

      if(vm.scope.selOriginType) {
        param += '&originType=' + vm.scope.selOriginType;
      }
      if(vm.scope.selAlarmType) {
        param += '&alarmType='+vm.scope.selAlarmType;
      }
      if(vm.scope.selResolveStatus) {
        param += '&resolveStatus='+vm.scope.selResolveStatus;
      }
      if(vm.scope.dateFrom) {
        param += '&searchDateFrom='+vm.scope.dateFrom.startOf('day');
      }
      if(vm.scope.dateTo) {
        param += '&searchDateTo='+vm.scope.dateTo.endOf('day');
      }
      if(vm.scope.selLevel) {
        param += '&level='+vm.scope.selLevel;
      }

      paasAlarmStatusService.alarmStatusList(param).then(
        function(result) {
          vm.issues = result.data;

          vm.scope.totalItems = result.data.totalCount;
          vm.scope.pageItems = result.data.pageItem;
          vm.scope.totalPages = Math.ceil(result.data.totalCount / result.data.pageItem);

          $location.search({});
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();

    vm.scope.pageChanged = function() {
      vm.getAlarmStatusList(vm.scope.currentPage);
    };


    // Go Container Detail
    vm.goAlarmStatusDetail = function(itemId) {
      var params = {
        'selOriginType' : vm.scope.selOriginType,
        'selAlarmType' : vm.scope.selAlarmType,
        'selLevel' : vm.scope.selLevel,
        'selResolveStatus' : vm.scope.selResolveStatus,
        'dateFrom' : vm.scope.dateFrom,
        'dateTo' : vm.scope.dateTo,
        'page' : vm.scope.currentPage
      }
      $location.path('/paas/alarm/status/'+itemId).search(params);
    };


    // Reload
    vm.scope.$on('broadcast:reload', function() {
      vm.scope.loading = true;
      vm.getAlarmStatusList(vm.scope.currentPage);
    });
  }


  angular
    .module('monitoring')
    .controller('PaasAlarmStatusDetailController', PaasAlarmStatusDetailController);

  /** @ngInject */
  function PaasAlarmStatusDetailController($scope, $timeout, $stateParams, $location, $exceptionHandler, common, paasAlarmStatusService) {
    var vm = this;
    vm.scope = $scope;
    vm.scope.loading = true;

    vm.alarmId = $stateParams;
    vm.stateParams = $location.search();
    vm.common = common;

    // Alarm Status Detail
    (vm.getAlarmStatusDetail = function() {
      paasAlarmStatusService.alarmStatusDetail(vm.alarmId.id).then(
        function(result) {
          vm.alarm = result.data;
          vm.alarmLevelClass = vm.alarm.level;
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    })();

    // Modal Setting
    vm.confirmReceipt = function() {
      vm.modalReset();
      vm.modalTitle = '';
      vm.modalMessage = ' ?';
      vm.confirmCallback = vm.alarmDetailReceipt;
    };
    vm.confirmActionModify = function(action) {
      vm.modalReset();
      vm.modalTitle = '';
      vm.modalMessage = '  ?';
      vm.actionId = action.id;
      vm.confirmCallback = vm.alarmActionUpdate;
      vm.callbackParam = action;
    };
    vm.confirmActionDelete = function(actionId) {
      vm.modalReset();
      vm.modalTitle = '';
      vm.modalMessage = '  ?';
      vm.actionId = actionId;
      vm.confirmCallback = vm.alarmActionDelete;
    };
    vm.modalReset = function() {
      vm.modalTitle = '';
      vm.modalMessage = '';
      vm.actionId = '';
      vm.confirmCallback = undefined;
      vm.callbackParam = undefined;
    };


    // Alarm Detail Cancel
    vm.cancel = function(path) {
      var params = {
        _o : vm.stateParams.selOriginType,
        _a : vm.stateParams.selAlarmType,
        _l : vm.stateParams.selLevel,
        _r : vm.stateParams.selResolveStatus,
        _f : vm.stateParams.dateFrom,
        _t : vm.stateParams.dateTo,
        _p : vm.stateParams.page
      }
      $location.path(path).search(params);
    };

    // Alarm Detail Receipt
    vm.alarmDetailReceipt = function() {
      vm.scope.loading = true;

      var body = {resolveStatus: "2"};

      paasAlarmStatusService.alarmStatusUpdate(vm.alarm.id, body).then(
        function() {
          vm.getAlarmStatusDetail();
          // $rootScope.$broadcast('broadcast:getAlarms');
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Alarm Action Complete
    vm.alarmActionComplete = function() {
      vm.scope.loading = true;

      if(vm.actionDesc == '' || vm.actionDesc == null || vm.actionDesc == undefined) {
        $exceptionHandler('', {code: null, message: '  .'});
        return;
      }

      var body = {resolveStatus: "3"};

      paasAlarmStatusService.alarmStatusUpdate(vm.alarm.id, body).then(
        function() {
          vm.alarmActionCreate();
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Alarm Action Create
    vm.alarmActionCreate = function() {
      vm.scope.loading = true;

      if(vm.actionDesc == '' || vm.actionDesc == null || vm.actionDesc == undefined) {
        $exceptionHandler('', {code: null, message: '  .'});
        return;
      }

      var body = {alarmId: vm.alarm.id, alarmActionDesc: vm.actionDesc};

      paasAlarmStatusService.alarmActionCreate(body).then(
        function() {
          vm.getAlarmStatusDetail();
          vm.actionDesc = '';
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    //
    vm.setUpdating = function(index) {
      vm.updating = index;
    };

    // Alarm Action Update
    vm.alarmActionUpdate = function() {
      vm.scope.loading = true;
      vm.updating = -1;

      var body = {alarmActionDesc: vm.callbackParam.alarmActionDesc};

      paasAlarmStatusService.alarmActionUpdate(vm.callbackParam.id, body).then(
        function() {
          vm.getAlarmStatusDetail();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Alarm Action Delete
    vm.alarmActionDelete = function() {
      vm.scope.loading = true;

      paasAlarmStatusService.alarmActionDelete(vm.actionId).then(
        function() {
          vm.getAlarmStatusDetail();
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );
    };

    // Reload
    vm.scope.$on('broadcast:reload', function() {
      vm.scope.loading = true;
      vm.getAlarmStatusDetail();
    });

  }


  angular
    .module('monitoring')
    .controller('PaasAlarmStatisticsController', PaasAlarmStatisticsController);

  /** @ngInject */
  function PaasAlarmStatisticsController($scope, $timeout, $location, $exceptionHandler, $document, $window, Excel,
                                         common, nvd3Generator, paasAlarmStatisticsService) {
    var vm = this;
    vm.scope = $scope;
    vm.scope.loading = true;

    var today = '';
    vm.interval = 1;
    vm.period = 'd';

    vm.scope.totalWidgets = {};
    vm.scope.servieWidgets = {};
    vm.scope.matrixWidgets = {};

    // Alarm Statistics
    (vm.getAlarmStatistics = function(condition) {
      var from, to;
      vm.period = condition;

      if(condition == 'd') {
        today = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'days'));
        vm.strPeriod = today;
      } else if(condition == 'w') {
        from = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'week').add(-1, 'week'));
        to = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'week'));
        vm.strPeriod = from.substring(0, today.lastIndexOf(' ')) + ' ~ ' + to.substring(0, today.lastIndexOf(' '));
      } else if (condition == 'm') {
        from = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'month').add(-1, 'month'));
        to = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'month'));
        vm.strPeriod = from.substring(0, today.lastIndexOf(' ')) + ' ~ ' + to.substring(0, today.lastIndexOf(' '));
      } else if (condition == 'y') {
        from = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'year').add(-1, 'year'));
        to = common.convertTimestampToDate(moment().add((Number(vm.interval)-1)*-1, 'year'));
        vm.strPeriod = from.substring(0, today.lastIndexOf(' ')) + ' ~ ' + to.substring(0, today.lastIndexOf(' '));
      }

      var params = '?period=' + vm.period;
      params += '&interval=' + vm.interval;

      // Statistics Info
      paasAlarmStatisticsService.alarmStatistics(params).then(
        function(result) {
          vm.stats = result.data;
          vm.scope.loading = false;
        },
        function(reason) {
          vm.scope.loading = false;
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );


      // Total Chart
      paasAlarmStatisticsService.alarmStatisticTotal(params).then(
        function(result) {
          vm.scope.setWidget(1, "Total", "alarmStatisticTotal", result.data);
        },
        function(reason) {
          vm.scope.setWidget(1, "Total", "alarmStatisticTotal", null);
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );

      // Service Chart
      paasAlarmStatisticsService.alarmStatisticService(params).then(
        function(result) {
          vm.scope.setWidget(1, "Service", "alarmStatisticService", result.data);
        },
        function(reason) {
          vm.scope.setWidget(2, "Service", "alarmStatisticService", null);
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );

      // Matrix Chart
      paasAlarmStatisticsService.alarmStatisticMatrix(params).then(
        function(result) {
          vm.scope.setWidget(1, "Matrix", "alarmStatisticMatrix", result.data);
        },
        function(reason) {
          vm.scope.setWidget(3, "Matrix", "alarmStatisticMatrix", null);
          $timeout(function() { $exceptionHandler(reason.data.message, {code: reason.data.HttpStatus, message: reason.data.message}); }, 500);
        }
      );

    })(vm.period);


    vm.scope.config = {visible: true};

    $timeout(function () {
      vm.scope.config.visible = true;
    }, 200);

    // Line Chart Create
    vm.scope.setWidget = function (id, title, func, jsonArr) {
      var widget = {
        col: 0, row: 0, sizeX: 1, sizeY: 1, id: id, type: "lineChart",
        chart: {
          options: nvd3Generator["lineChart"].options()
        },
        func: func
      };

      if(jsonArr) {
        var value, arr = [];
        var checkData = 0;
        for (var i in jsonArr) {
          value = jsonArr[i].stat == null ? [{time: 0, count: 0}] : jsonArr[i].stat;
          arr.push({values: value, key: jsonArr[i].name});
          for(var j in value) {
            if(value[j] != null) {
              if(checkData < value[j].count) {
                checkData = value[j].count;
              }
            }
          }
        }
        widget.chart.data = arr;
      } else {
        widget.chart.options.chart.forceY = false;
      }

      widget.chart.options.chart.xAxis.tickFormat = function(d) {
        if(vm.period == "d") {
          return d3.time.format('%H:%M')(new Date(d * 1000));
        } else if(vm.period == "w") {
          return d3.time.format('%y-%m-%d')(new Date(d * 1000));
        } else if(vm.period == "m") {
          return d3.time.format('%y-%m-%d')(new Date(d * 1000));
        } else if(vm.period == "y") {
          return d3.time.format('%y-%m')(new Date(d * 1000));
        }
      };

      widget.chart.options.chart.y = function (d){ return d.count; };
      widget.chart.options.chart.yAxis.tickFormat = function(d) { return d3.format("")(d); };
      widget.chart.options.chart.yAxis.axisLabel = "Count";
      widget.chart.options.chart.yAxis.axisLabelDistance = -5;
      widget.chart.options.chart.margin.left = 55;
      widget.axisLabel = "Count";

      if(title == "Total") {
        vm.scope.totalWidgets = widget;
      } else if(title == "Service") {
        vm.scope.servieWidgets = widget;
      } else if(title == "Matrix") {
        vm.scope.matrixWidgets = widget;
      }
    };

    // Interval Alarm Statistics
    vm.setInterval = function(arithmetic) {
      if(arithmetic == '-') {
        vm.interval--;
      } else {
        vm.interval++;
      }
      vm.getAlarmStatistics(vm.period);
    };


    // Alarm Status Page Move(Origin)
    vm.goListOriginType = function(originType, level) {
      goList(originType, '', level);
    };


    // Alarm Status Page Move(Type)
    vm.goListAlarmType = function(alarmType, level) {
      goList('', alarmType, level);
    };


    function goList(originType, alarmType, level) {
      var param = {
        _o: originType,
        _a: alarmType,
        _l: level
      };

      var from, to;
      var tmpYear, tmpMonth, tmpDay;
      if(vm.period == 'd') {
        var tmpDate = vm.strPeriod;
        from = new Date(tmpDate.slice(0,4), tmpDate.slice(5,7)-1, tmpDate.slice(8,10)).getTime();
        to = new Date(tmpDate.slice(0,4), tmpDate.slice(5,7)-1, tmpDate.slice(8,10)).getTime();
      } else if(vm.period == 'w') {
        var tmpFrom = vm.strPeriod.slice(0, vm.strPeriod.indexOf('~')-1);
        var tmpTo = vm.strPeriod.slice(vm.strPeriod.indexOf('~')+2);
        from = new Date(tmpFrom.slice(0,4), tmpFrom.slice(5,7)-1, tmpFrom.slice(8,10)).getTime();
        to = new Date(tmpTo.slice(0,4), tmpTo.slice(5,7)-1, tmpTo.slice(8,10)).getTime();
      } else if (vm.period == 'm') {
        tmpYear = vm.strPeriod.slice(0, 4);
        tmpMonth = parseInt(vm.strPeriod.slice(5), 10)-1;
        tmpDay = new Date().getDate();
        from = new Date(tmpYear, tmpMonth, tmpDay).getTime();
        to = new Date(tmpYear, tmpMonth+1, tmpDay).getTime();
      } else if(vm.period == 'y') {
        tmpYear = parseInt(vm.strPeriod.slice(0, 4))+1;
        tmpMonth = parseInt(vm.strPeriod.slice(5), 10)-1;
        tmpDay = new Date().getDate();
        from = new Date(tmpYear-1, tmpMonth, tmpDay).getTime();
        to = new Date(tmpYear, tmpMonth, tmpDay).getTime();
      }

      param['_f'] = from;
      param['_t'] = to;
      $location.path('/paas/alarm/status').search(param);
    }


    // Statistics Print
    vm.statisticsPrint = function(divName) {
      var dateContents = '<p class="date_box">' + vm.strPeriod + '</p>'
      var innerContents = $document[0].getElementById(divName).innerHTML;
      var popupWinindow = $window.open('', '_blank', 'width=1200,height=700,scrollbars=no,menubar=no,toolbar=no,location=no,status=no');
      popupWinindow.document.open();
      popupWinindow.document.write(
        '<!doctype html>' +
        '<head>' +
        '<meta charset="utf-8">' +
        '<title>IaaS / PaaS Monitoring</title>' +
        '<style>' +
        'p.date_box {width:280px;height:60px;font-size:20px;padding:16px 10px;text-align:center;border:1px solid #cccccc;margin-bottom:30px;}\n' +
        '.panel{margin-bottom:30px;}' +
        '</style>' +
        '<link rel="stylesheet" type="text/css" href="styles/vendor-min.css">' +
        '<link rel="stylesheet" type="text/css" href="styles/app-min.css">' +
        '</head>' +
        '<body onload="javascript:window.print();">'+ dateContents + innerContents +
        '</html>'
      );
      popupWinindow.document.close();
    };


    // Statistics Excel Download
    vm.excelDownload = function(divName) {
      var exportHref = Excel.tableToExcel(divName, 'Statistics');
      var link = $document[0].createElement('a');
      link.href = exportHref;
      link.download = 'Alarm_Statistics.xls';
      link.click();
    };

  }

})();

